<template>
  <div class="register">
    <nav-head title="注册"/>
    <div class="padding-0-25">
      <input class="phone" type="text" @input="handleInputNum" maxlength="11" v-model="phone" placeholder="请输入手机号">
      <div class="captcha-wrap relative">
        <input class="captcha" type="text" maxlength="6" v-model="captcha" placeholder="请输入短信验证码">
        <captcha-button :phone="phone" class="btn-captcha"></captcha-button>
      </div>
      <input class="pwd" type="password" maxlength="18" v-model="password" placeholder="请输入密码">
      <input class="invite" type="text" @input="handleInputNumLetter" maxlength="6" v-model="invite_code" placeholder="填写邀请码">
      <mt-button size="large" class="btn-register" v-bind:disabled="!phone || !captcha || !password ||!checked" @click.native="register" v-bind:class="{'available': phone && password && captcha && checked}">立即注册</mt-button>
      <!--<div class="agree-wrap">-->
        <!--<span class="checkbox" @click="getCaptcha" :class="{checked: checked}">同意</span><router-link to="/agreement" tag="a">《用户协议》</router-link>-->
      <!--</div>-->
    </div>
  </div>
</template>
<script>//
import NavHead from '@/components/NavHead.vue'
import CaptchaButton from '@/components/CaptchaButton'
import { Button, Toast } from 'mint-ui'
import { mapActions } from 'vuex'
import { REGISTER } from '@/vuex/Mine'
import Util from '@/tool/util'

export default {
  name: 'register',
  data () {
    return {
      phone: '',
      captcha: '',
      password: '',
      invite_code: '',
      checked: true
    }
  },
  methods: {
    getCaptcha () {
      if (this.checked) {
        this.checked = false
      } else {
        this.checked = true
      }
    },
    register () {
      if (this.phone && this.password && this.captcha && this.checked) {
        if (!Util.isMobile(this.phone)) {
          Toast('请输入正确的手机号!')
          return
        }
        if (this.password.length < 6) {
          Toast('请输入6-18位密码!')
          return
        }
        if (this.invite_code) {
          if (this.invite_code.length < 6) {
            Toast('邀请码为6位数，检查后重新填写!')
            return
          }
        }
        this.doRegister({
          username: this.phone,
          password: this.password,
          verification_code: this.captcha,
          invite_code: this.invite_code
        })
      }
    },
    handleInputNum (e) {
      this.phone = e.target.value.replace(/[^\d]/g, '')
    },
    handleInputNumLetter  (e) {
      this.invite_code = e.target.value.replace(/[^\d|a-zA-Z]/g, '')
    },
    ...mapActions({
      doRegister: REGISTER
    })
  },
  components: {
    NavHead,
    [Button.name]: Button,
    CaptchaButton
  }
}
</script>
<style scoped lang="scss">
  div.register {
    text-align: center;
    min-height: 100%;
    position: relative;
    width: 100%;
    background-color: $background-light-color;
    input[type='text'],
    input[type='password'] {
      border: none;
      outline: none;
      width: 100%;
      font-size: 16px;
      height: 56px;
      line-height: 56px;
      margin-bottom: 10px;
      background: none;
      border-radius: 0;
      color: $font-color;
      @if $IF365 { border-bottom: 1px solid $border-color; }
      @else { border-bottom: 1px solid #DDD; }
    }
    input.phone {
      @if $IF365 { background-image: url("../../assets/login/ic_count_365@2x.png"); }
      @else { background-image: url("../../assets/login/ic_count@2x.png"); }
      background-repeat: no-repeat;
      background-position: left center;
      background-size: auto 45%;
      padding-left: 9%;
    }
    input.captcha {
      @if $IF365 { background-image: url("../../assets/login/ic_yanzheng_365@2x.png"); }
      @else { background-image: url("../../assets/login/ic_yanzheng@2x.png"); }
      background-repeat: no-repeat;
      background-position: left center;
      background-size: auto 45%;
      padding-left: 9%;
    }
    input.pwd {
      @if $IF365 { background-image: url("../../assets/login/ic_num_365@2x.png"); }
      @else { background-image: url("../../assets/login/ic_num@2x.png"); }
      background-repeat: no-repeat;
      background-position: left center;
      background-size: auto 45%;
      padding-left: 9%;
    }
    input.invite {
      @if $IF365 { background-image: url("../../assets/login/ic_invite_365@2x.png"); }
      @else { display: none; }
      background-repeat: no-repeat;
      background-position: left center;
      background-size: auto 45%;
      padding-left: 9%;
    }
    .captcha-wrap {
      .btn-captcha {
        position: absolute;
        margin-top: 5px;
        right: 0;
        top: 0;
      }
    }
    .btn-register {
      border: none;
      background-color: #ccc;
      color: white;
      box-shadow: none;
      height: 48px;
      font-size: 17px;
      margin-top: 20px;
    }
    .btn-register.available{
      background-color: $back-color;
      @if $IF365 { color: #131313; }
    }
    .agree-wrap {
      font-size: 15px;
      height: 22px;
      line-height: 22px;
      text-align: left;
      margin-top: 20px;
      color: $back-color;
      span.checkbox{
        height: 22px;
        display: inline-block;
        background-repeat: no-repeat;
        background-size: auto 100%;
        background-position: left center;
        padding-left: 26px;
        @if $IF365 { background-image: url("../../assets/login/pick_off_365@2x.png"); }
        @else { background-image: url("../../assets/login/pick_off@2x.png"); }
      }
      span.checkbox.checked {
        @if $IF365 { background-image: url("../../assets/login/pick_on_365@2x.png"); }
        @else { background-image: url("../../assets/login/pick_on@2x.png"); }
      }
      a {
        color: $back-color;
      }
    }
  }
  .padding-0-25 {
    padding: 0 25px;
  }
  .relative {
    position: relative;
  }
</style>
