<template>
  <div class="captcha-button">
    <mt-button @click.native="getCaptcha" class="btn-captcha" :class="{disabled: disabled}">{{text}}</mt-button>
  </div>
</template>

<script>
import { Button, Toast } from 'mint-ui'
import { mapActions } from 'vuex'
import { GET_USER_CODE } from '@/vuex/Mine'
import Util from '@/tool/util'

const textArr = ['获取验证码', '等待']
export default {
  name: 'captchaButton',
  data () {
    return {
      disabled: false,
      text: textArr[0]
    }
  },
  props: ['phone'],
  methods: {
    getCaptcha () {
      if (Util.isMobile(this.phone)) {
        let times = 60
        const countDown = () => {
          setTimeout(() => {
            times--
            this.text = textArr[1] + times
            if (times === 0) {
              this.disabled = false
              this.text = textArr[0]
            } else {
              countDown()
            }
          }, 1000)
        }
        if (!this.disabled) {
          this.disabled = true
          this.text = textArr[1] + times
          // 发送短信API
          this.getUserCode({
            username: this.phone
          })
          countDown()
        }
      } else {
        Toast('请输入正确的手机号!')
      }
    },
    ...mapActions({
      getUserCode: GET_USER_CODE
    })
  },
  components: {
    [Button.name]: Button
  }
}
</script>
<style scoped lang="scss">
  .captcha-button button.btn-captcha {
    background-color: $back-color;
    @if $IF365 { color: #131313; }
    @else { color: white; }
    font-size: 16px;
  }
  .captcha-button button.disabled {
    background: #ddd;
    color: #999;
  }
</style>
